import Vue from 'vue'
import { requestGainBankList, requestGainMoney } from '@/api/API'
import BottomBar from '../../components/BottomBar'
import Header from '../../components/Header'
import { Field, Popup, Picker } from 'mint-ui'
Vue.component(Field.name, Field)
Vue.component(Popup.name, Popup)
Vue.component(Picker.name, Picker)

export default {
  name: 'withdraw',
  components: {
    Header, BottomBar
  },
  data () {
    return {
      card_info: {
        user_name: '',
        bank_card: '',
        bank_province: '',
        bank_city: '',
        bank_name: '',
        branch_name: '',
        money: null
      },
      can_bring_money: 0,
      bankList: [],
      isShowlog: false,
      slots: [
        {
          flex: 1,
          values: [],
          className: 'slot1',
          textAlign: 'center'
        }
      ],
      selectId: null,
      selectedId: null
    }
  },
  mounted () {
    let user = this.$store.state.User.user || {}
    this.can_bring_money = user.can_bring_money
    this.card_info.user_name = user.real_name || ''
    this.getBankList()
  },
  methods: {
    onShowLog () {
      this.isShowlog = !this.isShowlog
    },
    async getBankList () {
      let user = this.$store.state.User || {}
      let res = await requestGainBankList(user.token)
      if (res.code) {
        let data = []
        res.data.map(obj => {
          let text = (obj.user_name || '') + ' ' + obj.bank_name + ' ' + obj.bank_card
          data.push(Object.assign({text: text}, obj))
        })
        this.bankList = data
        this.slots[0].values = data
      }
    },
    onValuesChange (picker, values) {
      this.selectId = values[0]
    },
    onConfirm () {
      this.selectedId = this.selectId
      this.isShowlog = false
      this.card_info = Object.assign(this.card_info, this.selectedId)
    },
    async onWithdraw () {
      if (!this.card_info.user_name) {
        return window.EventBus.$emit('globalMessage', '请填写姓名')
      }
      if (!this.card_info.bank_province) {
        return window.EventBus.$emit('globalMessage', '请填写省份')
      }
      if (!this.card_info.bank_city) {
        return window.EventBus.$emit('globalMessage', '请填写城市')
      }
      if (!this.card_info.bank_name) {
        return window.EventBus.$emit('globalMessage', '请填写开户行')
      }
      if (!this.card_info.branch_name) {
        return window.EventBus.$emit('globalMessage', '请填写支行')
      }
      if (!this.card_info.bank_card) {
        return window.EventBus.$emit('globalMessage', '请填写银行卡号')
      }
      if (!this.card_info.money) {
        return window.EventBus.$emit('globalMessage', '请填写提现金额')
      }
      if (parseFloat(this.card_info.money) > parseFloat(this.can_bring_money)) {
        return window.EventBus.$emit('globalMessage', '提现金额不能大于余额')
      }
      let user = this.$store.state.User || {}
      let res = await requestGainMoney(user.token, user.uuid, this.card_info.user_name, this.card_info.bank_card, this.card_info.bank_province, this.card_info.bank_city, this.card_info.bank_name, this.card_info.branch_name, this.card_info.money)
      window.EventBus.$emit('globalMessage', res.code + '123321')
      if (res.code === 200) {
        this.$router.replace('profile')
      }
      window.EventBus.$emit('globalMessage', res.msg)
    }
  }
}



